<template>
  <b-modal centered v-model="show" hide-footer size="custom" class="modal-sm">
    <template v-slot:modal-header>
      <h1>Войти в систему</h1>
    </template>
    <form class="form" @submit.prevent="authUser">
      <b-form-input
        type="text"
        required
        class="login"
        :class="{ error: $v.login.$error }"
        v-model.trim="$v.login.$model"
        placeholder="Адрес эл. почты или номер телефона"
      />

      <b-form-input
        type="password"
        required
        class="password"
        :class="{ error: $v.password.$error }"
        v-model.trim="$v.password.$model"
        placeholder="Пароль"
      />

      <button type="submit" class="submit">Войти</button>

      <div>
        <router-link to="/registration" class="link"
          >Зарегистрироваться</router-link
        >

        <router-link to="/forgotPassword" class="link"
          >Забыли пароль?</router-link
        >
      </div>
    </form>
  </b-modal>
</template>

<script>
import { required, minLength } from "vuelidate/lib/validators";

export default {
  name: "Authorization",
  data: () => ({
    login: null,
    password: null,
    show: true
  }),
  methods: {
    async authUser() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        // some
        return 0;
      }

      let status = await this.$store.dispatch("loginUser", {
        login: this.login,
        password: this.password
      });

      if (status === 1) this.$router.push("/requests");
    }
  },
  validations: {
    login: {
      minLength: minLength(4),
      required
    },
    password: {
      minLength: minLength(6),
      required
    }
  }
};
</script>

